<template>
  <div class="all-container info-container" style="height:calc(100%);margin-top:0em;padding:0;width: 100%;">
    <div class="info-div" >
      <div class="info-header info-header-operate" style="right: 0em;top: 0;left:0;padding:0.2em 1.5em">
        <div class="operate-div" style="float:left">
          <selects  :option="selectOpt"  @getSelect="getOption"></selects>
        </div>
        <div class="operate-div" style="margin-right:0">
        <!--   <button class="button-operate"   @click="searchClick" style="margin-right:0;">
            <i class="hasIcon"  style="margin-top: 0;"><icon name="search"  style="font-size: 1.4em;"></icon></i>
            搜索
          </button> -->
          <datePicker ref="timer"  type='doubleDate' style="margin-right:0;"
                    :dateOpt="timeOpt" @getDatePicker="getOption"></datePicker>
        </div>
      </div>
      <div class="list-content detail-content" style="height: 35%;margin-top:4.3em;padding:0.5em 1.5em !important">
        <div class="tabs-show tab-div floatRightTab"  >
          <ul>
            <li v-for="(tabs, $index) in tabOpt" :class="{'selectedLi':  $index == tabIndex}"
                @click="getBtn(tabs, $index)">
              <img :src="tabs.img" />
              <span >{{tabs.text}} </span>
            </li>
          </ul>
        </div>
        <div id="barChart" class="barChart"></div>
      </div>
      <div class="list-content detail-content" style="height:calc(65% - 6.5em);padding:0.5em 1.5em !important;margin-top:0em">
        <div class="div-header" style="padding-top:0">
          <span >区域用{{deviceText}}量排行</span>
          <template v-for="(select, $index) in rankSelect">
            <selects :option="select"  @getSelect="getOption" class="info-select" style="margin-left:1em; float:right; font-size:0.94em"></selects>
          </template>
        </div>
        <div class="all_foot_con1">
          <table class="all_foot_ta" style="padding-right: 20px">
            <tr style="background: #305ea0">
              <td width="5%">排序</td>
              <td width="15%">户号</td>
              <td width="15%">户名</td>
              <td width="10%">用{{deviceText}}量</td>
              <td width="25%">用{{deviceText}}地址</td>
              <td width="10%">用{{deviceText}}性质</td>
              <td width="10%">变压器号</td>
              <td width="10%">楼栋号</td>
            </tr>
          </table>
          <div class="overflow" style="width: calc(100% + 0px)">
            <VuePerfectScrollbar style="position: relative;height:100%;width:100%">
              <table class="all_foot_ta " >
                <tr v-for="(item, $index) in tableData" style="font-size: 0.94em">
                  <td width="5%">{{$index + 1}}</td>
                  <td width="15%">{{item.meterUserNo}}</td>
                  <td width="15%">{{item.customerName}}</td>
                  <td width="10%">{{item.pr0===null?'--':item.pr0}}</td>
                  <td width="25%">{{item.installAddr}}</td>
                  <td width="10%">{{item.dictItemName}}</td>
                  <td width="10%">{{item.transformerNo}}</td>
                  <td width="10%">{{item.buildingNo}}</td>
                </tr>
              </table>
            </VuePerfectScrollbar>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
//  import echarts from 'echarts'
   export default{
       data() {
           return {
             refreshTable: true,
             tabIndex: 0,
             searchDate: '',
             deviceType: '07',
             line: '',
             tabOpt: [
               {img: 'dist/img/analyse/building.png',  text: '楼栋', value: 'building'},
               {img: 'dist/img/analyse/transformer.png', text: '变压器', value: 'transform'},
             ],
             tableData: [],
             barData: '',
             pageSize: 10,
             sortOrder: 'desc'
           }
       },
       computed: {
         timeOpt: function () {
           let dates = this.initDate();
           return {
             key: 'searchDate',
             value: {startDate: dates.start, endDate: dates.end},
             width: '28em',
             fontSize: '0.8em'
           }
         },
         selectOpt: function () {
           return {
             cs: 'client',
             width: '8em',
             key: 'deviceType',
             data: this.getEnumItem('deviceType').obj
           }
         },
         deviceText: function () {
           return this.deviceType === '07' ? '电': (this.deviceType === '08'? '水' : '气')
         },
         rankSelect: function () {
             return [
               {
                 cs: 'client',
                 width: '10em',
                 key: 'pageSize',
                 data: [
                   // {text: '5位', value: 5},
                   {text: '10位', value: 10},
                   {text: '20位', value: 20},
                   {text: '30位', value: 30},
                   {text:'50位', value: 50},
                   {text:'100位', value: 100}
                 ]
               },
               {
                 cs: 'client',
                 width: '10em',
                 key: 'sortOrder',
                 data: [
                   {text: '用' + this.deviceText + '量最高', value: 'desc'},
                   {text: '用' + this.deviceText + '量最低', value: 'asc'}
                 ]
               }
             ]
         }
       },
       methods: {
         getBtn: function (tab, index) {
           this.tabIndex = index;
           this.getBar();
         },
         getTable: function () {
            let that = this;
            that.searchDate = that.searchDate === '' ? that.initDate() : that.searchDate;
            let arr = that.searchDate.split('至');
            that.getRequest({
               url: 'comprehensive/queryElecdata',
               method: 'post',
               param: {sortOrder:that.sortOrder, pageSize: that.pageSize, startDate: arr[0], endDate: arr[1], deviceType: that.deviceType},
               success: function (data) {
                   that.refreshTable = true;
                   that.tableData = data.data;
               }
            });
         },
         getBar: function () {
           let that = this;
           that.searchDate = that.searchDate === '' ? that.initDate() : that.searchDate;
           let arr = that.searchDate.split('至');
           that.getRequest({
             url: 'comprehensive/queryElechart',
             param: {startDate: arr[0], endDate: arr[1], deviceType: that.deviceType, transformerNo: that.tabIndex === 0 ? '01' : '02'},
             method: 'post',
             success: function (data) {
              that.barData = data.data;
              that.makeBar();
             }
           });

         },
         makeBar: function () {
           let that = this;
           let option = {
             legend: {
               show: false,
               itemWidth: 55,
               padding: [5, 20, 15, 10],
               x: 'right',
               top: 20,
               textStyle: {
                 color: '#000'
               }
             },
             toolbox: {
               show: false,
               feature: {
                 dataZoom: {
                   yAxisIndex: 'none'
                 },
                 restore: {},
                 saveAsImage: {}
               }
             },
             grid: {
               left: '3%',
               right: '0.5%',
               top: 30,
               bottom: 20
             },
             xAxis: {
               type: 'category',
               data: that.barData[that.tabOpt[that.tabIndex].value].xData,
               axisLabel: {
                 textStyle: {
                   color: '#fff'
                 }
                 // formatter: function (value, index) {
                 //   if (value.length < 5) {
                 //     return value
                 //   }
                 //   return value.substr(5)
                 // }
               },
               axisTick: {
                 show: true,
                 lineStyle: {
                   color: 'rgba(255,255,255,0.15)'
                 }
               },
               splitLine: {
                 lineStyle: {
                   color: 'rgba(255,255,255,0.15)'
                 }
               },
               axisLine: {
                 show: true,
                 lineStyle: {
                   color: 'rgba(255,255,255,0.15)'
                 }
               }
             },
             yAxis: {
               type: 'value',
               name: that.deviceType === '07' ? '电量（kwh）' : (that.deviceType === '08' ? '水量（T）' : '气（Nm3）'),
               nameTextStyle: {
                 color: '#fff',
                 fontSize: that.getFontSize()
               },
               // max: function(value) {
               //   return value.max - 20;
               // },
               axisLabel: {
                 formatter: '{value}',
                 textStyle: {
                   color: '#fff'
                 }
               },
               splitLine: {
                 lineStyle: {
                   color: 'rgba(255,255,255,0.15)'
                 }
               },
               axisTick: {
                 show: false
               },
               axisLine: {
                 show: false
               }
             },
             tooltip: {
               trigger: 'axis',
               borderRadius: 100,
               backgroundColor: '#704451',
               padding: [25, 8],
               axisPointer: {
                 type: 'shadow',
                 label: {
                   backgroundColor: 'transparent'
                 }
               },
               formatter: function (params) {
                 return params[0].data + 'kwh';
               },
               textStyle: {
                 color: '#fff',
                 fontWeight: 600,
                 fontSize: 16
               }
             },
             series: {
               name: '',
               type: 'bar',
               barMaxWidth: 100,
               itemStyle: {
                 normal: {
                   color: new echarts.graphic.LinearGradient(
                     0, 0, 0, 1,
                     [
                       {offset: 0, color: '#519CE7'},
                       {offset: 0.7, color: '#4782C1'},
                       {offset: 1, color: '#3B6A9A'}
                     ]
                   )
                 },
                 emphasis: {
                   color: new echarts.graphic.LinearGradient(
                     0, 0, 0, 1,
                     [
                       {offset: 0, color: '#F96362'},
                       {offset: 0.5, color: '#CC5A5E'},
                       {offset: 1, color: '#974F5A'}
                     ]
                   )
                 }
               },
               data: that.barData[that.tabOpt[that.tabIndex].value].yData || [0,0,0],
               markPoint: {
                 symbolSize: [55, 37],
                 symbolOffset: [0, -20],
                 data: [
                   {
                     type: 'max',
                     name: '最大值',
                     symbol: 'image://dist/img/analyse/popupTop.png',
                     symbolOffset: [0, '-55%'],
                     label: {
                       normal: {
                         padding: [10, 10, 15, 10],
                         color: '#fff'
                       }

                     }
                   },
                   {
                     type: 'min',
                     name: '最小值',
                     symbol: 'image://dist/img/analyse/popupBottom.png',
                     label: {
                       normal: {
                         padding: [10, 10, 15, 10],
                       }

                     }
                   }
                 ]
               }
             }
           };
           that.line = echarts.init(document.getElementById('barChart'));
           that.$nextTick(function () {
             that.line.setOption(option);
           })
         },
         getFontSize: function () {
           let fontSize = 13.5;
           let width = window.screen.width;
           let currentWidth = document.body.clientWidth;
           if(width <= 1366 || currentWidth <= 1366) {
             fontSize = 11;
           } else if(width <= 1700 || currentWidth <= 1700) {
             fontSize = 12;
           } else  {
             fontSize = 13.5;
           }
           return fontSize
         },
         searchClick: function () {
           this.tabIndex = 0;
           this.getBar();
           this.getTable();
         },
         windowChange: function() {
           let that = this;
            if (that.line !== '') {
                that.line.resize();
             }
         }
       },
       mounted: function () {
           let Date = this.initDate();
           this.searchDate = this.searchDate === '' ? Date.start + '至'  + Date.end : this.searchDate;
           this.getBar();
           this.getTable();
           //let that = this;
           // window.onresize = function () {
           //   if (that.line !== '') {
           //     that.line.resize();
           //   }
           // }
       },
       watch: {
           deviceType: {
             handler() {
               let Date = this.initDate();
               this.refreshTable = false;
               this.tabIndex = 0;
               this.pageSize = 10;
               this.sortOrder = 'desc';
               let startDate = Date.start + '至' + Date.end;
               if (this.searchDate === startDate) {
                 this.getBar();
                 this.getTable();
               } else {
                 this.$refs.timer.dateOpt.value = {startDate: Date.start, endDate: Date.end};
                 this.$refs.timer.init();
                 this.searchDate = Date.start + '至' + Date.end;
               }
             }
           },
           searchDate: {
            handler() {
              this.tabIndex = 0;
              this.getBar();
              this.getTable();
            }
           },
           pageSize: {
             handler() {
               if (this.refreshTable) {
                 this.getTable();
               }
             }
           },
           sortOrder: {
             handler() {
               if (this.refreshTable) {
                 this.getTable();
               }
             }
           }
       }
   }
</script>

<style lang="scss" scoped="scoped">
.floatRightTab{
  float:right!important;
  margin:0!important;
  li{
    margin-top:-1px;
    padding:0.23em 1.1em!important;
    img{
      width:1.4em;
      height:1.4em;
      margin-top:0.3em
    }
    img+span{
      margin-top:0.35em;
    }
  }
}
  .barChart{
    width:100%;
    height: calc(100% - 2.5em);
    float:left;
    margin-top:0.5em
  }
  .div-header{
    padding: 0.5em 0 ;
    span{
      float:left;
      line-height: 3;
      font-size: 0.875em;
    }
  }
  .div-header:after{
    clear:both;
    content: '';
    width: 1px;
    height:0;
    display: block;
  }
.all_foot_con1 {
  border: 1px solid rgba(255,255,255,0.4);
  border-radius: 2px;
  position: relative;
  float: left;
  margin-bottom: 1.2em;
  overflow: hidden;
  height: calc(100% - 4.5em);
  width: 100%;
  .all_foot_ta {
    width: 100%;
    text-align: center;
    line-height: 1.4em;
    overflow: scroll;
    height: 1.4em;
    margin-bottom:0;
    margin-left: 0;
  }
  .all_foot_ta td {
    padding: 0.5em 0;
    border-right: 1px solid rgba(255,255,255,0.4);
    font-size:0.9em;
  }
  .all_foot_ta td:last-child{
    border-right:0
  }
  .all_foot_ta tr{
    border-bottom:1px solid rgba(255,255,255,0.4);
    padding: 5px 0
  }
}
.overflow {
  overflow: hidden;
  width: 100%;
  height: calc(100% - 33px);
  overflow-x: hidden;
}
.overflow .all_foot_ta tr{
  cursor:pointer;
}
.overflow .all_foot_ta tr:hover {
  background:#227fdf;
  color:white
}
</style>



